	import Dialog from '../../wxcomponents/vant/dist/dialog/dialog';
	import Toast from '../../wxcomponents/vant/dist/toast/toast';
	var  app = getApp().globalData
    export default {
        data() {
            return {
				swiperCurrent:0,
                showActive:false,
				loading:false,
				allowloading:false,
				page:1,
				hotList:[], //服务商
				swiperList:[],
				list:[],
				listTransition:[],
				screenWidth:'',
				left:0,
				boxWidth:0,
				finish:false,
				showTitle:false,
				ids:'',
				ad:'',
				showScroll:false
            }
        },
		onShareAppMessage:function(res) {
			return {
			  title: '德申汇服务商',
			  imageUrl:app.shareImg,
			  path: '/pages/service/service'
			}
		},
		onPullDownRefresh(){
			this.ids = ''
			this.page = 1
			this.getHot()
			setTimeout(function () {
			        uni.stopPullDownRefresh();
					uni.showToast({
					    title: '刷新成功',
					    duration: 1000,
						icon:'none'
					});
			  }, 1000);
		},
		onReachBottom(){
			if(this.allowloading){
				
				this.ids = ''
				for(var val of this.hotList){
					this.ids += ','+val.id
				}
				this.ids = this.ids.substr(1)
				this.allowloading = false
				this.loading = true
				this.page++
				this.$nextTick(()=>{
					this.getHot()
				})
			}
		},
		onHide() {
			this.showActive = false
		},
		onLoad(e) {
			const screenWidth = uni.getSystemInfoSync().screenWidth;
			this.screenWidth = screenWidth
			// 获取轮播图数据
			this.getData()
			// 获取服务商函数
			this.getHot()
		},
		onShow() {
			if(uni.getStorageSync('service')){
				for(var val of this.hotList){
					if(val.id==JSON.parse(uni.getStorageSync('service')).companyId){
						val.view = JSON.parse(uni.getStorageSync('service')).view
					}
				}
				this.$nextTick(function(){
					uni.removeStorageSync('service')
				})
			}
		},
        methods: {
			company(item){
				uni.navigateTo({
					url:'/pagesTwo/MyBusiness/MyBusiness?id='+item.id
				})
			},
			provider(item1){
				uni.navigateTo({
					url:'/pagesFour/ServiceProvider/ServiceProvider?id='+item1.id
				})
			},
			MyInfo(id){
				uni.navigateTo({
					url:'/pagesMy/MyInfo/MyInfo?id='+id
				})
			},
			myservice(){
				if(uni.getStorageSync('userId')){
					if(uni.getStorageSync('setInfo') ===0){
						uni.showToast({
						    title: '请完善个人信息',
						    duration: 2000,
							icon:'none'
						});
						
						setTimeout(function() {
							uni.navigateTo({
								url:'/pages/PersonalIdcard/PersonalIdcard'
							})
						}, 1000);
						
					}else{
						uni.navigateTo({
							url:'/pagesFour/MyService/MyService'
						})
						// this.$api.myself_company({
						// 	token: uni.getStorageSync("token"),
						// 	user_id: uni.getStorageSync("userId")
						// }).then(res=>{
						// 	// console.log(res)
						// 	if(res.data.code === '1054'){
						// 		uni.navigateTo({
						// 			url:'../../pagesTwo/EnterpriseCertification/EnterpriseCertification'
						// 		})
						// 	}else if(res.data.code === '200'){
						// 		uni.setStorageSync('company_id',Number(res.data.data.id))
						// 		uni.navigateTo({
						// 			url:'/pagesFour/MyService/MyService'
						// 		})
						// 	}
						// })
						
						
						
					}
					
				}else{
					uni.clearStorageSync()
					uni.setStorageSync('path',this.$url.getCurrentPageUrlWithArgs())
					uni.showToast({
					    title: '请登录',
					    duration: 2000,
						icon:'none'
					});
					setTimeout(function() {
						uni.reLaunch({
							url: '/pages/login/login'
						})
					}, 1000);
				}
			},
			// 修改轮播图的 分页器样式用的
			changeSwiper(e) {
			    this.swiperCurrent = e.detail.current;
			},
			swiper(item) {
				if(item.href_type==0){
					uni.navigateTo({
						url:'/pagesFour/ServiceDetails/ServiceDetails?id='+item.href_id + '&status=0&user_id=' + item.user_id
					})
				}else{
					uni.navigateTo({
						url:'/pagesMy/view/view?url='+ item.href
					})
				}
			   // console.log(item)
			},
			getHot () {
			  this.$api.sp_recommend({
					  limit:20,
					  page: this.page,
					  ids:this.ids
					}).then(res => {
						// console.log(res)
							if(res.data.code=== '200'){
								// uni.hideLoading()
								if(this.page===1){
									this.hotList = res.data.data
									if(res.data.data.length>=20){
										this.allowloading= true
										this.loading =false
									}else{
										this.allowloading= false
										this.loading =false
										this.showTitle = true
									}
								}else{
									this.hotList = this.hotList.concat(res.data.data)
									if(res.data.data.length>=20){
										this.allowloading= true
										this.loading =false
									}else{
										this.allowloading= false
										this.loading =false
										this.showTitle = true
									}
								}
								
							}
							this.$nextTick(()=>{
								this.finish = true
							})
					})
					
			},
			scroll(e){
				var x = this.boxWidth-this.screenWidth
				this.left=e.detail.scrollLeft/x*20
			},
			toupper(){
				// this.left=0
			},
			tolower(){
				// this.left=20
			},
			getData(){
				this.$api.service_cate_list({
				}).then(res=>{
					console.log(res)
					if(res.data.data.cate.length>10){
						this.showScroll = true
					}else{
						this.showScroll = false
					}
					this.listTransition = this.arrayTransition(2,res.data.data.cate)
					this.boxWidth = parseInt(this.listTransition.length/5*this.screenWidth)
					this.ad = res.data.data.ad
				})
			},
            // 二维数组转换
			arrayTransition(n,arr){
				// 转换的二维数组
				var listTransition = []
				for(var i= 0;i<arr.length;i=i+n){
					listTransition.push(arr.slice(i,i+n))
				}
				return listTransition
			},
			// 搜索
			search(){
				uni.navigateTo({
					url:'/pagesFour/ServiceProvider/ServiceProvider'
				})
			},
			goMy(){
				// console.log(123)
				if(uni.getStorageSync('setInfo') ==0){
					uni.showToast({
					    title: '请完善个人信息',
					    duration: 2000,
						icon:'none'
					});
					
					setTimeout(function() {
						uni.navigateTo({
							url:'../../pages/PersonalIdcard/PersonalIdcard'
						})
					}, 1000);
					
				}else{
					// console.log(456)
					if (uni.getStorageSync('isVip') == 0) {
						Dialog.confirm({
						  title: '提示',
						  message: '您还不是VIP用户,去支付？',
						  confirmButtonText:'去支付'
						}).then(() => {
							uni.navigateTo({
								url:'../../pagesMy/pay/pay'
							})
						}).catch(() => {
						  // on cancel
						});
					  } else if (uni.getStorageSync('isVip') == 1) {
						  // uni.navigateTo({
						  // 	url:'../../pagesMy/ceshi/ceshi'
						  // })
							uni.navigateTo({
								url:'../../pagesMy/MyProject/MyProject'
							})
					  } 
				}
			},
				
			goClub(){
				if(uni.getStorageSync('setInfo') ===0){
					uni.showToast({
					    title: '请完善个人信息',
					    duration: 2000,
						icon:'none'
					});
					
					setTimeout(function() {
						uni.navigateTo({
							url:'../../pages/PersonalIdcard/PersonalIdcard'
						})
					}, 1000);
					
				}else{
					if (uni.getStorageSync('isVip') === 0) {
						Dialog.confirm({
						  title: '提示',
						  message: '您还不是VIP用户,去支付？',
						  confirmButtonText:'去支付'
						}).then(() => {
							uni.navigateTo({
								url:'../../pagesMy/pay/pay'
							})
						}).catch(() => {
						  // on cancel
						});
					  } else if (uni.getStorageSync('isVip') === 1) {
						uni.navigateTo({
							url:'../../pagesTwo/Club/Club'
						})
					  } 
				}
				
			},
			goEnterprise(){
				if(uni.getStorageSync('setInfo') === 0){
					uni.showToast({
					    title: '请完善个人信息',
					    duration: 1000,
						icon:'none'
					});
					setTimeout(function() {
						uni.navigateTo({
							url:'../../pages/PersonalIdcard/PersonalIdcard'
						})
					}, 1000);
				}else{
					this.$api.MyselfIndex({
						token: uni.getStorageSync("token"),
						user_id: uni.getStorageSync("userId"),
						
					}).then(res=>{
						if (res.data.code === '200') {
						  if (res.data.data.company_data) {
						    this.company_id = res.data.data.company_data.id
							uni.setStorageSync('company_id',Number(this.company_id))
						  }
						  this.$nextTick(() => {
						    if (uni.getStorageSync("company_id")) {
						      uni.navigateTo({
						      	url:'../../pagesTwo/MyBusiness/MyBusiness?id='+uni.getStorageSync("company_id")
						      })
						    }else{
								uni.navigateTo({
									url:'../../pagesTwo/MyBusiness/MyBusiness'
								})
							} 
						  })
						}
					})
					
				}
			},
			goTradingVolume(){
				uni.navigateTo({
					url:'../../pagesTwo/TradingVolume/TradingVolume'
				})
			}
        }
    }
